.website-c .overlay,
.website-c .loadingPage {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 10;
    -webkit-transition: 0.1s ease;
    -o-transition: 0.1s ease;
    transition: 0.1s ease;
    opacity: 0;
    visibility: hidden;
}
.website-c .overlay.active,
.website-c .loadingPage.active {
    opacity: 1;
    visibility: visible;
}
.website-c .loadingPage {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: rgba(255, 255, 255, 0.95);
    z-index: 999;
}

.lang-language span {
    cursor: pointer;
    padding-right: 5px;
}

.social-shares > div {
    display: inline-block;
    cursor: pointer;
    margin-right: 5px;
}

.content-responsive img {
    height: auto !important;
}

#showMessage .alert-success {
    padding: 10px;
}


